@import 'style';
@import 'style/mixins';

.Comments {
  & {   
    @media (max-width: 420px) {
      width: 100%;
    }

    @media (min-width: 421px) {
      width: 380px;
    }

    text-align: left;
  }

  .Comments__list {
    width: 100%;
    margin-bottom: 32px;

    display: flex;
    flex-direction: column;
    gap: 16px;

    text-wrap: wrap;
  }
}
