@import 'style';
@import 'style/mixins';

.ReturnButton {
  @include text-style;

  & {
    display: flex;
    gap: 8px;
    align-items: center;

    background-color: rgba(255, 255, 255, 0);

    width: 79px;
    margin-bottom: 16px;
    padding: 12px 0;
    border: 0;

    color: $return-button-color;

    cursor: pointer;
  }
}
