@import 'style';
@import 'style/mixins';

.LikeIcon {
  width: 24px;
  height: 24px;
  padding: 0;
  border: 0;
  background-color: inherit;
  box-sizing: border-box;

  cursor: pointer;

  svg {
    fill: $white;
  }  

  &.LikeIcon_active {
    svg {
      fill: $heart-color;
      stroke-opacity: 0;
    }
  }
}
