@import 'style';
@import 'style/mixins';

.PostCreatePage {
  &{
    @include page-styles;
    width: 100%;
    min-height: 91.8vh;
    background-color: $app-background;
  
    .PostCreatePage__wrapper {
      display: flex;
      flex-direction: column;
      text-align: left;
  
      align-self: center;
  
      .Title {
        margin-bottom: 24px;
      }
  
      @media (max-width: 520px) {
          width: 100%;
      }
    }
  }
}
