@import 'style';
@import 'style/mixins';

.SubscriptionsPage {
  & {
    @include page-styles;
    width: 100%;
    min-height: 91.8vh;
    padding-top: 24px;
    
    background-color: $app-background;
  
    .SubscriptionsPage__wrapper {
      display: flex;
      flex-direction: column;
      gap: 24px;
      text-align: left;
  
      align-self: center;
  
      @media (max-width: 520px) {
          width: 100%;
      }
    }
  }
}
