@import 'style';
@import 'style/mixins';

.SubscribeIcon {
  & {
    width: 40px;
    height: 40px;

    background-color: $light-gray;

    border: 0;
    border-radius: 8px;

    cursor: pointer;

    position: relative;
    z-index: 2;

    svg {
      position: relative;
      z-index: 1;
    }
  }

  &.SubscribeIcon_active {
    background-color: $primary;
  }
}
