@import 'style';
@import 'style/mixins';

.PostPage {
  @include page-styles;

  & {
    @include page-styles;
    width: 100%;
    background-color: $app-background;
  
    .PostPage__wrapper {
      display: flex;
      flex-direction: column;
      text-align: left;
  
      align-self: center;
  
      .Title {
        margin-bottom: 24px;
      }

      .Title {
        margin-bottom: 16px;
      }
  
      @media (max-width: 520px) {
          width: 100%;
      }

      .PostCard {
        background-color: rgba(255, 255, 255, 0);
        border: 0;
    
        .PostCard__content {
          margin-top: 24px;
          padding: 0;
          gap: 16px;
    
          .PostCard__tags {
            margin-top: 0;
          }
        }
    
        .PostCard__footer {
          padding: 0;
        }
      }

      .UserCard {
        margin-top: 24px;
      }
    
      .Comments {
        margin-top: 24px;
      }
    }

    .ReturnButton {
      margin-bottom: 8px;
    }
  }
}
