@import 'style';
@import 'style/mixins';


.SearchField {
  @include input-field-style;

  & {   
    width: 100%;
    margin: 0;
    display: flex;
    flex-direction: column;
    align-items: center;

    position: relative;
  
    input {
      border: 1px solid $gray;
      padding-left: 36px;
    }
  }
}
