@import 'style';
@import 'style/mixins';

.BurgerMenu {
  @include ui-styles;

  &{
    width: 100%;
    max-width: none;
    min-height: calc(100vh - 51px);
    
    align-items: flex-start;  
    background-color: white;  
    position: fixed;
    z-index: 10000;

    font-weight: 400;

    @media (min-width: 481px) {
      font-size: 18px;
    }
  
    @media (min-width: 1024px) {
      font-size: 20px;
    }

    @media (min-width: 1400px) {
      font-size: 21px;
    }

    &.BurgerMenu_active {
      opacity: 0;
      animation: appearance 600ms ease 0s 1 forwards;

      @keyframes appearance {
        0%{
          opacity: 0;
          transform: translateY(-30px);
        }
        100%{
          opacity: 1;
          transform: translateY(-1px);
        }
      }
    }
  
    &.BurgerMenu_inactive {
      animation: disappearance 600ms ease 0s 1 forwards;
      @keyframes disappearance {
        0%{
          opacity: 1;
          transform: translateY(-1px);
        }
        100%{
          opacity: 0;
          transform: translateY(-30px);
        }
      }
    }
  }

  ul {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
    gap: 16px;

    margin: 0;
    padding: 32px 16px;
    
    @media (min-width: 481px) {
      padding-left: 50px;
      gap: 22px;
    }
  
    @media (min-width: 1024px) {
      padding-left: 150px;
      gap: 26px;
    }
  }

  .BurgerMenu__item {
    display: flex;
    cursor: pointer;
  }

  .BurgerMenu__footer {
    &{
      width: calc(100% - 20px);

      margin: auto;

      padding: 16px 0;
  
      border-top: 1px solid #0000001A;
  
      box-sizing: border-box;

      @media (min-width: 481px) {
        padding-left: 50px;
        padding-bottom: 24px;
      }
    
      @media (min-width: 1024px) {
        padding-left: 150px;
      }
    }
   
    button {
      border: 0;
      background-color: inherit;

      padding: 12px 12px 12px 20px;

      font-family: inherit;
      font-size: inherit;
      font-weight: 500;
      color: $danger;

      cursor: pointer;

      @media (min-width: 481px) {
        font-size: 18px;
      }
    
      @media (min-width: 1024px) {
        font-size: 20px;
      }

      @media (min-width: 1400px) {
        font-size: 24px;
      }
    }
  }
}
