@import 'style';
@import 'style/mixins';

.ProfilePage {
  & {
    @include page-styles;
    width: 100%;
    min-height: 91.8vh;
    padding-top: 24px;
    
    background-color: $app-background;

  
    .ProfilePage__wrapper {
      display: flex;
      flex-direction: column;
      text-align: left;
  
      align-self: center;

      .Title {
        margin-bottom: 24px;
      }

      & :not(:first-child):not(:last-child) {
        margin-bottom: 24px;
      }
  
      @media (max-width: 520px) {
          width: 100%;
      }

      .ReturnButton {
        margin-top: -20px;
        margin-bottom: 8px;
      }
    }
  }
}
